
import XDialog from 'vux/src/components/x-dialog/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Checklist from 'vux/src/components/checklist/index.vue'

import Calendar from '@/components/Calendar'
import commonBtn from '@/components/commonBtn'
export default {
name: 'signIn',
components: {
commonBtn, Calendar,  Checker, CheckerItem,XDialog,Popup,Checklist
},
directives: {
TransferDom
},
data(){
return{
trueStatus:true,
btnStatus:true,
arr:[],

agoDayHide:'1000000000',
futureDayHide:'1000000000',
totalNum:'5',
signInShow:false,
payTypeStatus:false,
payNum:5,
payType:'1',
}
},
methods:{
clickDay(data) {

console.log(this.filterArr(this.arr,data))
if(this.filterArr(this.arr,data).length > 0){
if(this.filterArr(this.arr,data)[0].className == 'signMark'){
this.$vux.toast.text('已签到~', 'middle')
}else {

this.payTypeStatus = true;
}
}else {
this.$vux.toast.text('请选择正确的日期进行签到~', 'middle')
}

},
initDayHide(){

let monthData = new Date(); 
monthData.setDate(1);
monthData.setHours(0);
monthData.setSeconds(0);
monthData.setMinutes(0);
let nextMonthData = new Date(); 
if (monthData.getMonth() == 12){
nextMonthData.setMonth(1)
}else{
nextMonthData.setMonth(monthData.getMonth() + 1)
}
nextMonthData.setDate(1);
nextMonthData.setHours(0);
nextMonthData.setSeconds(0);
nextMonthData.setMinutes(0);
console.log('本月第一天0点' + monthData.getTime()/1000)
console.log('本月最后一天23点' + nextMonthData.getTime())

this.agoDayHide = String(parseInt(monthData.getTime()/1000));
this.futureDayHide = String(parseInt(nextMonthData.getTime()/1000) - 1)
},
filterArr(arr,data){
return arr.filter(item=>item.date == data)
},
btnChange(){
console.log(this.btnStatus)
if(!this.btnStatus){
this.$vux.toast.text('请不要重复签到~', 'middle')
}
this.btnStatus = false;
},
submitPay(){

this.payTypeStatus = false;
this.signInShow = true;  
},
changePayType () {
console.log(this.payType)
},
},
created () {
this.initDayHide();
this.arr = [
{date:'2019/4/1',className:"signMark"},
{date:'2019/4/2',className:"signMark"},
{date:'2019/4/3',className:"signMark"},
{date:'2019/4/4',className:"signMark"},
{date:'2019/4/5',className:"signMarkNone"},
{date:'2019/4/6',className:"signMark"},
{date:'2019/4/7',className:"signMark"},
{date:'2019/4/8',className:"signMark"},
{date:'2019/4/9',className:"signMark"},
{date:'2019/4/10',className:"signMark"},
{date:'2019/4/11',className:"signMark"},
{date:'2019/4/12',className:"signMark"},
{date:'2019/4/13',className:"signMark"},
{date:'2019/4/14',className:"signMark"},
{date:'2019/4/15',className:"signMark"},
{date:'2019/4/16',className:"signMark"},
{date:'2019/4/17',className:"signMark"},
{date:'2019/4/18',className:"signMark"},
{date:'2019/4/19',className:"signMark"},
{date:'2019/4/20',className:"signMark"},
{date:'2019/4/21',className:"signMark"},
{date:'2019/4/22',className:"signMarkNone"},
{date:'2019/4/23',className:"signMarkNone"},
{date:'2019/4/24',className:"signMarkNone"},
{date:'2019/4/25',className:"signMarkNone"},
{date:'2019/4/26',className:"signMarkNone"},
{date:'2019/4/27',className:"signMarkNone"},
{date:'2019/4/28',className:"signMarkNone"},
{date:'2019/4/29',className:"signMarkNone"},
{date:'2019/4/30',className:"signMarkNone"},
];
}

}



